(function(){
  'use strict';
  //var trie = require('trie-search');
  //var ts = new TrieSearch();

  
  let cache = {};
  let CommentRow = React.createClass({
    render: function() {
    	let body = highlightText(this.props.comment.body, this.props.filter);
     	let name = highlightText(this.props.comment.name, this.props.filter);


      function highlightText(source, filter) {
        var highlightText = [];
        if (!filter) {
          highlightText.push(source); 
        } else {  
          var regex = new RegExp(filter, 'gi');
          var segments = source.split(regex);
          var replacements  = source.match(regex);
          segments.forEach(function(segment, i){
              highlightText.push(segment);
              if(i < segments.length -1) {
                highlightText.push(React.DOM.span({
                    className: 'highlight', key: i
                }, replacements[i]));  
              }
          }.bind(this));
          cache["yaniv"] = "yaniv_aharon";
          console.log(cache["yaniv"]);
        }
        return highlightText;
      }

      return (
        <div className="comment">
          <div className="commentBody">{body}</div>
          <div className="commentName">{name}</div>
        </div>
      );
    }
  });

  let CommentsTable = React.createClass({
    render: function() {
      let rows = [];

      this.props.comments.forEach(function(comment) {
        if (comment.body.toLowerCase().indexOf(this.props.filterText) === -1
        	&&
        	comment.name.toLowerCase().indexOf(this.props.filterText) === -1) {
          return;
        }
        rows.push(<CommentRow comment={comment} key={comment.id} filter={this.props.filterText.toLowerCase()} />);
        
      }.bind(this));
      return (
       <div>
       	{rows}
       </div>
      );
    }
  });

  let SearchBar = React.createClass({
    handleChange: function() {
      this.props.onUserInput(
        this.refs.filterTextInput.value
        
      );
    },
    render: function() {
      return (
        <form>
          <input
            className="searchBar"
            type="text"
            placeholder="Search Comment..."
            value={this.props.filterText}
            ref="filterTextInput"
            onChange={this.handleChange}
          />
        </form>
      );
    }
  });

  let FilterableComments = React.createClass({
    getInitialState: function() {
      return {
        filterText: '',
        comments : []
      };
    },

    handleUserInput: function(filterText) {
      this.setState({
        filterText: filterText
      });
    },

    componentDidMount: function() {
  	axios.get(this.props.source)
    	.then(function (response) {
      	let comments = response.data.slice(0, 10);
        //console.log(comments);
        //ts.addFromObject(comments);
      	this.setState({
        		comments: comments
      	});
    	}.bind(this))
    	.catch(function (error) {
      	console.log(error);
    	});
    },

    render: function() {
      return (
        <div>
          <SearchBar
            filterText={this.state.filterText}
            onUserInput={this.handleUserInput}
          />
          <CommentsTable
            comments={this.state.comments}
            filterText={this.state.filterText.toLowerCase()}
          />
        </div>
      );
    }
  });


  ReactDOM.render(
    <FilterableComments source="http://jsonplaceholder.typicode.com/comments" />,
    document.getElementById('container')
  );

})();


